.BlogPost {
    flex: 1 1;
    display: flex;
    flex-direction: column;
    margin: 0 3.3% 40px;
}

.BlogPost p {
    font-family: "Spectral Light", serif;
    font-size: 16px;
    margin: 8px 0;
}

.BlogPost .BlogPost__Pull-Quotes {
    margin: 8px 0;
}

.BlogPost .BlogPost__Title {
    margin: 4px 0 8px;
    order: -1;
}

@media only screen and (min-width: 768px) {
    .BlogPost {
        margin: 0 5.5% 60px;
    }

    .BlogPost p {
        font-size: 18px;
        padding-left: 40%;
    }

    .BlogPost .BlogPost__Pull-Quotes {
        padding-right: 20%;
    }

    .BlogPost .BlogPost__Title {
        order: -2;
        color: transparent;
        -webkit-text-stroke-color: black;
        -webkit-text-stroke-width: 1px;
    }
}