.About {
	flex: 1 1;
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	flex-wrap: wrap;
	margin: 0 3.3% 40px;
}

.About .About__Flex-container--first,
.About .About__Flex-container--second {
	flex: 1 1 100%;
}

.About p {
	font-family: "Spectral Light", serif;
	font-size: 16px;
	margin: 8px 0;
}

.About h2 {
	line-height: 1;
	margin: 8px 0 16px;
	order: -1;
}

@media only screen and (min-width: 768px) {
	.About {
		margin: 0 5.5% 60px;
		padding-right: 5%;
		flex-wrap: nowrap;
	}

	.About .About__Flex-container--first {
		flex: 1 1 50%;
		padding-right: 5%;
	}

	.About .About__Flex-container--second {
		flex: 1 1 40%;
	}

	.About p {
		font-size: 18px;
	}
}