.ImgWrapper {
    display: flex;
    box-sizing: border-box;
}

.BlogPost__ImgWrapper {
    margin: 4px 0;
}

.BlogPost__ImgWrapper--first {
    margin: 4px 0;
    order: -2;
}

.BlogPost__ImgWrapper--right {
    margin: 4px 0;
}

.Img {
    width: 100%;
    height: auto;
}

@media only screen and (min-width: 768px) {
    .HomeLinkImgWrapper {
        display: none;
    }

    .BlogPost__ImgWrapper {
        margin: 8px 0;
    }

    .BlogPost__ImgWrapper--first {
        margin: 8px 0;
        order: -1;
    }

    .BlogPost__ImgWrapper--right {
        margin: 8px 0;
        padding-left: 40%;
    }
}