.BtnToTop {
    position: fixed;
    bottom: 150px;
    right: 3.3%;
    padding: 0 6px;
    font-size: 40px;
    border: solid 2px black;
    background: white;
    color: black;
    visibility: hidden;
    opacity: 0;
    transition: all 300ms ease-in;
    z-index: 900;
}

.BtnToTop.BtnToTop--show {
    opacity: 1;
    visibility: visible;
}

@media only screen and (min-width: 768px) {
    .BtnToTop {
        padding: 4px 11px 3px;
        font-size: 50px;
        right: 5.5%;
    }
}