.SecondaryNav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0 3.3%;
    box-sizing: border-box;
    position: fixed;
    top: 60px;
    transform: translateY(-100%);
    transition: transform 200ms ease-in;
    background: white;
    z-index: 998;
}

.SecondaryNav.SecondaryNav--show {
    transform: translateY(0%);
}

.SecondaryNavLinkContainer {
    display: flex;
    flex-direction: column;
}

.SecondaryNavLink {
    white-space: nowrap;
    text-transform: uppercase;
    color: black;
    text-decoration: none;
    box-sizing: border-box;
}

.SecondaryNavLink:first-child {
    display: none;
}

.SecondaryNavLinkIcon {
    padding-right: 10px;
}

.SecondaryNavCloseBtn {
    position: absolute;
    right: 5%;
    color: white;
    display: none;
    background: transparent;
    border: none;
}

.SecondaryNavCloseBtn:focus {
    outline: none;
}

@media only screen and (min-width: 375px) {
    .SecondaryNav {
        top: 65px;
    }
}

@media only screen and (min-width: 480px) {
    .SecondaryNav {
        top: 70px;
    }
}

@media only screen and (min-width: 540px) {
    .SecondaryNav {
        top: 75px;
    }
}

@media only screen and (min-width: 768px) {
    .SecondaryNav {
        padding: 0 5.5%;
        top: 0;
        background: black;
        z-index: 999;
        position: absolute;
    }

    .SecondaryNavLink {
        color: white;
    }

    .SecondaryNavLink:first-child {
        display: block;
    }

    .SecondaryNavLink.HasComma {
        padding-right: 2%;
    }

    .SecondaryNavLink.HasComma h1:after {
        content: ",";
        font-size: inherit;
    }

    .SecondaryNavLinkContainer {
        flex-direction: row;
        flex-wrap: wrap;
        margin-right: 6%;
    }

    .SecondaryNavLinkIcon {
        display: none;
    }

    .SecondaryNavCloseBtn {
        display: block;
    }
}